import React, { useEffect, useState } from "react";
import { Col } from "react-bootstrap";
import Axios from "axios";

function About() {
  const [listOfUsers, setListOfUsers] = useState([{}]);

  // JavaScript way of making a get request by fetching the end point
  // fetch("http://localhost:3001/getUsers")
  //   .then((response) => response.json())
  //   .then((data) => {
  //     setListOfUsers(data);
  //   })
  //   .catch((error) => console.log(error));

  // Using Axios to fetch the data from end point
  useEffect(() => {
    Axios.get("http://localhost:3001/getUsers").then((response) => {
      setListOfUsers(response.data);
    });
  }, []);

  return (
    <Col>
      {listOfUsers.map((user) => {
        return (
          <div>
            <h1>FirstName: {user.firstName}</h1>
            <h1>LastName: {user.lastName}</h1>
            <h1>Address: {user.address}</h1>
            <h1>PostalCode: {user.postalCode}</h1>
            <h1>PhoneNumber: {user.phone}</h1>
            <h1>Email: {user.email}</h1>
            <hr />
          </div>
        );
      })}
    </Col>
  );
}

export default About;
